<template>
    <div class="carousel-item" :style="styles" >
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name : 'carousel-item',
        props: {
            img: String
        },
        computed: {
            styles(){
                let styles = {}
                if(this.img){
                    styles['background-image'] = `url('${this.img}')`
                }
                return styles;
            }
        }
