
<template lang="pug">
    include _welcome.pug
</template>

<script>
    import 'assets/img/ss-codex-phpdoc.png'
    import 'assets/img/ss-codex-document.png'
    import 'assets/img/issue-addon-scan.png'
    import 'assets/img/issue-speed-improvements.png'
    import 'assets/img/issue-toolbar-styles.png'

    import 'assets/img/slides/slide-html.png'
    import 'assets/img/slides/slide-typewriter.jpg';

    import welcome from './_welcome.pug'

    export default {
        name: 'c-welcome',
        mixins : [Vue.codex.mixins.layout, Vue.codex.mixins.resize],
//        template: welcome(),
        data(){
            return {
                carouselHeight: 9999
            }
        },
        mounted(){
            this.updateHeight();
            this.$on('resize', () => this.updateHeight());
            this.$nextTick(() => {
                this.$$el.css('display', 'block')
            });

            this.$$ready(() => {
                this.$nextTick(() => codex.loader.stop())
                let $header = $(this.$refs.header.$el);
                this.$$('.scrollspy').scrollSpy({
                    scrollOffset: $header.outerHeight(true) + 1
                }); //.on('scrollSpy:enter', console.log.bind(console)).on('scrollSpy:exit', console.log.bind(console))
            });
        },
        methods: {
            updateHeight(){
                this.carouselHeight = this.getViewPort().height + 1
            }
        }
    }
</script>


