<template>
    <section class="c-section" :class="classes">
        <slot v-if="fullWidth"></slot>
        <div v-if="fullWidth === false" class="container">
            <slot></slot>
        </div>
    </section>
</template>
<script>

    export default {
        name: 'c-section',
        props: {
            fullWidth: { type: Boolean, default:false},
            scheme: { type: [String,Boolean], default: false },
            title: String,
            titleTag: {type:String, default: 'h2'}
        },

        data() {
            return {
                classes: 'c-section-light',
                schemes: {
                    light: 'c-section-light',
                    dark: 'c-section-dark'
                }
            }
        },

        mounted(){
        },
        computed: {
            classes(){
                let cls = ['c-section'];
                if(this.scheme === false) return '';
                if(Object.keys(this.schemes).indexOf(this.scheme) === -1){
                    return console.warn('Scheme "%s" not valid for section.', this.scheme)
                }
                return this.schemes[this.scheme];
            }

        },
        methods: {

        }
    }
</script>



